<template>
<div class="products_block">

    <div class="category_containerm mgt10"
    v-for="category in this.categories"
    v-bind:key="category.id"
    >
    <div class="category_name">
        {{ category.name }}
    </div>
    <!-- products -->
    <div class="products__list__wrapper">
    <ProductCard
    v-for="product in this.getProductsByCategory(category)"
    v-bind:key="product.id"
    v-bind:product="product"
    >
    </ProductCard>
    {{ count }}
    </div>
    <!-- endof products -->

    </div>

</div>
</template>

<script>
import ProductCard from '../components/ProductCard.vue';

export default {
  name: 'Products',
  components: {
      ProductCard,
  },
  mounted () {
      console.log('mount products page')
  },
  computed: {
    categories () {
        return this.$store.state.categories
    },
    products () {
        return this.$store.state.products.filter(
            product => product.parent == null
        )
    },
    products_in_cart () {
        return this.$store.state.products_in_cart;
    },
    count () {
        return this.$store.state.count;
    }
  },
  methods: {
      getProductsByCategory(category) {
          return this.products.filter(
              pr => pr.category == category.id
          )
      },
  }
}
</script>

